import React from 'react';
import './Building.css';
import BuildingView from '../BuildingView/BuildingView'
import RoofTypeImage from './RoofTypeImage/RoofTypeImage'
import FoundationTypeImage from './FoundationTypeImage/FoundationTypeImage'
import MaterialList from '../MaterialList/MaterialList'
import Dropdown from 'react-dropdown';
import { IconContext } from "react-icons";
import { BsFillInfoCircleFill } from "react-icons/bs";
import ReactTooltip from 'react-tooltip';

const roofTypeOptions = [
  'Saddel', 'Fladt', 'Valm', 'Skråt'
]

const foundationTypeOptions = [
  'Linje', 'Punkt', 'Stribe'
]

class Building extends React.Component {
  constructor(props) {
    super(props)

    this.handleAreaChange = this.handleAreaChange.bind(this)
    this.handleHeightChange = this.handleHeightChange.bind(this)
    this.handleRoofTypeSelect = this.handleRoofTypeSelect.bind(this)
    this.handleFoundationTypeSelect = this.handleFoundationTypeSelect.bind(this)
  }

  handleAreaChange(inputArea) {
    const area = inputArea.target.value;
    this.props.onAreaChange(area)
  }

  handleHeightChange(inputHeight) {
    const height = inputHeight.target.value;
    this.props.onFloorHeightChange(height)
  }

  handleRoofTypeSelect(inputRoofType) {
    const roofType = inputRoofType.value;
    this.props.onRoofTypeChange(roofType)
  }

  handleFoundationTypeSelect(inputFoundationType) {
    const foundationType = inputFoundationType.value;
    this.props.onFoundationTypeChange(foundationType)
  }

  render() {

    const area = this.props.building.area
    const floors = this.props.building.floors
    const floorHeight = this.props.building.floorHeight
    const totalArea = area*floors
    const perimeter = Math.floor(Math.sqrt(area)*4)
    const wallArea = Math.floor(perimeter*(floors*floorHeight))
    const roofArea = Math.floor(area*1.1)
    const windowArea = Math.floor(wallArea*0.15)
    const built = this.props.building.built
    const age = 2021-built

    if (this.props.building.selectAddress === true && this.props.building.selectEdit === '') {
    return (
      <div className="Building">
        <div className='BuildingHeader'>
          <h1>Bygningsinformation</h1>
          <IconContext.Provider value={{size: "1.6rem", color: "white", className: "informationInfo" }}>
                <div>
                  <BsFillInfoCircleFill data-tip="" data-for='4'/>
                  <ReactTooltip place="right" type="info" effect="solid" border='true' id='4'>
                    <ol className='ToolTipList' type='A'>
                      <li>Værdier med mørkeblå baggrund kan redigeres</li>
                    </ol>
                  </ReactTooltip>
                </div>
          </IconContext.Provider>
        </div>
        <div className="image-container">
          <BuildingView building={this.props.building}/>
        </div>
        <h2 className='address-title'>{this.props.building.address}</h2>
        <div className='information-container'>
          <div className='paravar'>
            <p className='parameter'><span>Etageareal</span></p>
            <input className='cVariable' value={area} pattern="[0-9]*" onChange={this.handleAreaChange}/>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Etagehøjde</span></p>
            <input className='cVariable' value={floorHeight} pattern="[0-9]*" onChange={this.handleHeightChange}/>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Etager</span></p>
            <p className='variable'><span>{floors}</span></p>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Samlet areal</span></p>
            <p className='variable'><span>{totalArea}</span></p>
          </div>
        </div>
        <div className='information-container' id='i2'>
          <div className='paravar'>
            <p className='parameter'><span>Omkreds</span></p>
            <p className='variable'><span>{perimeter}</span></p>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Vægareal</span></p>
            <p className='variable'><span>{wallArea}</span></p>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Tagareal</span></p>
            <p className='variable'><span>{roofArea}</span></p>
          </div>
        </div>
        <div className='information-container' id='i3'>
          <div className='paravar'>
            <p className='parameter'><span>Vinduesareal</span></p>
            <p className='variable'><span>{windowArea}</span></p>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Opført</span></p>
            <p className='variable'><span>{built}</span></p>
          </div>
          <div className='paravar'>
            <p className='parameter'><span>Alder</span></p>
            <p className='variable'><span>{age}</span></p>
          </div>
        </div>
        <div className='typeContainer'>
          <div className='compTypeContainer'>
            <h2>Tagtype</h2>
            <Dropdown options={roofTypeOptions} onChange={this.handleRoofTypeSelect} placeholder={this.props.building.roofType} />
            <RoofTypeImage roofType={this.props.building.roofType}/>
          </div>
          <div className='compTypeContainer'>
            <h2>Fundamenttype</h2>
            <Dropdown options={foundationTypeOptions} onChange={this.handleFoundationTypeSelect} placeholder={this.props.building.foundationType} />
            <FoundationTypeImage foundationType={this.props.building.foundationType}/>
          </div>
        </div>
        <div className='EnergyHeader'>
          <h1>Energi</h1>
          <IconContext.Provider value={{size: "1.6rem", color: "white", className: "energyInfo" }}>
                <div>
                  <BsFillInfoCircleFill data-tip=""/>
                  <ReactTooltip place="right" type="info" effect="solid" border='true'>
                    <ol className='ToolTipList' type='A'>
                      <li>1) Data er baseret på resultater fra Energistyrelsen</li>
                      <li>2) Energiklassen kan variere fra A-F, hvor A er bedst</li>
                      <li>3) Det beregnede forbrug er et estimat, og kan være væsentligt større eller mindre i realiteten</li>
                      <li>4) Forbedring beskriver en forbedring der kan foretages, som vil reducere energiforbrugt i bygningen</li>
                      <li>5) Investering er den sum penge der kræver at implementere forbedringen</li>
                      <li>6) Potentiel besparelse er den årlige besparelse som den implementerede forbedring vil medføre</li>
                    </ol>
                  </ReactTooltip>
                </div>
          </IconContext.Provider>
        </div>
        <div className='energyContainer'>
          <div className='EIC'>
            <p className='energyData'>Energiklasse:</p><p className='energyData2'>{this.props.building.energy[0]}</p>
            <p className='energyData'>Beregnet forbrug:</p><p className='energyData2'>{this.props.building.energy[2]} kr/år</p>
          </div>
          <div className='EIC'>
            <p className='energyData'>Potentiel besparelse:</p><p className='energyData2'>{this.props.building.energy[1]} kr/år</p>
            <p className='energyData'>Investering:</p><p className='energyData2'>{this.props.building.energy[3]} kr</p>
            <p className='energyData'>Forbedring:</p><p className='energyData2'>{this.props.building.energy[4]}</p>
          </div>
        </div>
        {/*<Download building={this.props.building}/>*/}
        <div className='MaterialSection'>
          <MaterialList building={this.props.building}/>
        </div>
      </div>
    )
    } else {
      return(
        <div></div>
      )
    }
  }
}
export default Building;