import React from 'react'
import './RoofTypeImage.css'
import saddel from './Saddel.jpg'
import valm from './Valm.jpg'
import skråt from './Skråt.jpg'
import fladt from './Fladt.jpg'

class RoofTypeImage extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        if (this.props.roofType === 'Saddel') {
            return <img src={saddel}/>
        } else if (this.props.roofType === 'Fladt') {
            return <img src={fladt}/>
        } else if (this.props.roofType === 'Skråt') {
            return <img src={skråt}/>
        } else if (this.props.roofType === 'Valm') {
            return <img src={valm}/>
        } else {
            return <div></div>
        }
    }
}

export default RoofTypeImage