import React from 'react'
import { db } from "../../util/firebase";
import './updateButton.css'

class UpdateButton extends React.Component {
    constructor(props) {
        super(props)
        
        this.updateData = this.updateData.bind(this)
        this.updateData2 = this.updateData2.bind(this)
        this.updateData3 = this.updateData3.bind(this)
    }

    updateData() {
        const wall = this.props.materials.wall
        const roof = this.props.materials.roof
        const floor = this.props.materials.floor
        const foundation = this.props.materials.foundation
        const inwall = this.props.materials.inwall
        const windows = this.props.materials.windows

            return db
            .ref(`${this.props.building.address}/materials`)
            .update(
                {wall,roof,floor,foundation,inwall,windows},
            )
            .then(_ => {
                this.setState({ wall: this.props.materials.wall,
                                roof: this.props.materials.roof,
                                floor: this.props.materials.floor,
                                foundation: this.props.materials.foundation,
                                inwall: this.props.materials.inwall,
                                windows: this.props.materials.windows});
            })
      }

    updateData2() {
        const roof = this.props.building.roofType
        const foundation = this.props.building.foundationType

            return db
            .ref(`${this.props.building.address}/types`)
            .update(
                {roof,foundation},
            )
            .then(_ => {
                this.setState({ roof: this.props.building.roofType,
                                foundation: this.props.building.foundationType});
            })
      }

    updateData3() {
        const area = this.props.building.area

            return db
            .ref(this.props.building.address)
            .update(
                {area},
            )
            .then(_ => {
                this.setState({ area: this.props.building.area});
            })
      }

      updateData4() {
          const roofS = this.props.materials.roofS
          const wallS = this.props.materials.wallS
          const floorS = this.props.materials.floorS
          const foundationS = this.props.materials.foundationS
          const inwallS = this.props.materials.inwallS

          return db
            .ref(`${this.props.building.address}/materials`)
            .update(
                {roofS,wallS,floorS,foundationS,inwallS},
            )
            .then(_ => {
                this.setState({ roofS: this.props.building.roofS,
                                wallS: this.props.building.wallS,
                                floorsS: this.props.building.floorsS,
                                foundationS: this.props.building.foundationS,
                                inwallS: this.props.building.inwallS});
            })
      }

    render() {
    if (this.props.materials.wall.length === 0 || this.props.materials.roof.length === 0 || this.props.materials.floor.length === 0 || this.props.materials.foundation.length === 0 || this.props.materials.inwall.length === 0) {
            return(
                <div></div>
            )
        } else {
            return (
            <div className='buttonDivUpdate'>
                <a className='buttonUpdate' onClick={() => {this.updateData(); this.updateData2(); this.updateData3(); this.updateData4();}}>Gem</a>
            </div>
            )
        }
    }
}

export default UpdateButton