import React from 'react'
import './ResultsSimple.css'
import { Bar } from 'react-chartjs-2';
import Impacts from '../Impacts/Impacts'
import { simpleRoof, simpleWall, simpleFloor, simpleFoundation, simpleInWall, roofTypeValue, foundationTypeValue } from '../../functions/simpleFunctions'

class ResultsSimple extends React.Component {
    constructor(props) {
        super(props)

        this.sumMaterials = this.sumMaterials.bind(this)
        this.removeMissing = this.removeMissing.bind(this)
        this.getLabels = this.getLabels.bind(this)
        this.getData = this.getData.bind(this)
    }

    sumMaterials(array) {
        const newArray = []
        const Beton = ['Beton',0]
        const Træ = ['Træ',0]
        const Skifer = ['Skifer',0]
        const Tegl = ['Tegl',0]
        const Isolering = ['Isolering',0]
        const Tagpap = ['Pap',0]
        const Mursten = ['Mursten',0]
        const Gips = ['Gips', 0]
        const Metal = ['Metal', 0]
        const Sten = ['Sten', 0]

        for (var x of array) {
            if (x[0] === 'Træ') {
                Træ[1] = Træ[1]+x[1]
            }
            if (x[0] === 'Beton') {
                Beton[1] = Beton[1]+x[1]
            }
            if (x[0] === 'Skifer') {
                Skifer[1] = Skifer[1]+x[1]
            }
            if (x[0] === 'Tegl') {
                Tegl[1] = Tegl[1]+x[1]
            }
            if (x[0] === 'Isolering') {
                Isolering[1] = Isolering[1]+x[1]
            }
            if (x[0] === 'Tagpap') {
                Tagpap[1] = Tagpap[1]+x[1]
            }
            if (x[0] === 'Mursten') {
                Mursten[1] = Mursten[1]+x[1]
            }
            if (x[0] === 'Gips') {
                Gips[1] = Gips[1]+x[1]
            }
            if (x[0] === 'Metal') {
                Metal[1] = Metal[1]+x[1]
            }
            if (x[0] === 'Sten') {
                Sten[1] = Sten[1]+x[1]
            }             
        }
        newArray.push(Beton) 
        newArray.push(Træ)
        newArray.push(Skifer)
        newArray.push(Tegl)
        newArray.push(Isolering)
        newArray.push(Tagpap)
        newArray.push(Mursten)
        newArray.push(Metal)
        newArray.push(Gips)
        newArray.push(Sten)
        return newArray
    }

    removeMissing(array) {
        const newArray = []
        for (var x of array) {
            if (x[1] !== 0) {
                newArray.push(x)
            }
        } return newArray
    }

    getLabels(array) {
        const newArray = []
        for (var x of array) {
            newArray.push(x[0])
        } return newArray
    }

    getData(array) {
        const newArray = []
        for (var x of array) {
            newArray.push(x[1])
        } return newArray
    }

    render() {

         //-------------------------------------VARIABLES-----------------------------------------------
         //#region
         const area = this.props.building.area
         const floors = this.props.building.floors
         const totalArea = area*floors
         const perimeter = Math.floor(Math.sqrt(area)*4)
         const wallArea = perimeter*(floors*2.5)
         const roofArea = Math.floor(area*roofTypeValue(this.props.building.roofType))
         const foundationArea = area*foundationTypeValue(this.props.building.foundationType)
         const windowArea = Math.floor(wallArea*0.15)
         //#endregion
         
         //-----------------------------------MATERIAL AMOUNTS------------------------------------------
         //#region
         const wallMaterialAmountS = simpleWall(this.props.materials.wallS).map((item) => <li>{item[0]}: {(item[1]*wallArea).toFixed(2)}m3</li>)
         const wallMaterialAmountS2 = simpleWall(this.props.materials.wallS).map((item) => [item[0],item[1]*wallArea])

         const roofMaterialAmountS = simpleRoof(this.props.materials.roofS).map((item) => <li>{item[0]}: {(item[1]*roofArea).toFixed(2)}m3</li>)
         const roofMaterialAmountS2 = simpleRoof(this.props.materials.roofS).map((item) => [item[0],item[1]*roofArea])

         const floorMaterialAmountS = simpleFloor(this.props.materials.floorS).map((item) => <li>{item[0]}: {(item[1]*totalArea).toFixed(2)}m3</li>)
         const floorMaterialAmountS2 = simpleFloor(this.props.materials.floorS).map((item) => [item[0],item[1]*totalArea])

         const foundationMaterialAmountS = simpleFoundation(this.props.materials.foundationS).map((item) => <li>{item[0]}: {(item[1]*foundationArea).toFixed(2)}m3</li>)
         const foundationMaterialAmountS2 = simpleFoundation(this.props.materials.foundationS).map((item) => [item[0],item[1]*foundationArea])

         const inWallMaterialAmountS = simpleInWall(this.props.materials.inwallS).map((item) => <li>{item[0]}: {(item[1]*wallArea*0.5).toFixed(2)}m3</li>)
         const inWallMaterialAmountS2 = simpleInWall(this.props.materials.inwallS).map((item) => [item[0],item[1]*wallArea*0.5])

         const mergedS = [].concat.apply([], [wallMaterialAmountS2,roofMaterialAmountS2,floorMaterialAmountS2,foundationMaterialAmountS2,inWallMaterialAmountS2]);
         const mergedMaterialsS = this.removeMissing(this.sumMaterials(mergedS))

         const mergedMaterialAmountS = mergedMaterialsS.map((item) => <li>{item[0]}: {item[1].toFixed(2)}m3</li>)
         //#endregion

         //---------------------------------------CHART-------------------------------------------------
         //#region 
         const chartData = this.getData(mergedMaterialsS)
         const chartLabels = this.getLabels(mergedMaterialsS)

         const data = {
            labels: chartLabels,
            datasets: [
              {
                label: '',
                data: chartData,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                  'rgba(153, 102, 255, 1)',
                  'rgba(255, 159, 64, 1)',
                ],
                borderWidth: 1,
              },
            ],
          }
          
          const options = {
            scales: {
              yAxes: [
                {
                  ticks: {
                    beginAtZero: true,
                  },
                },
              ],
            },
          }
        //#endregion

        return(
            <div>
                <h1 className='ResultHeader'>Materialemængder</h1>
                <div className='ResultContainer'>
                    <div className='aResult'>
                        <p>Ydervægge</p>
                        {wallMaterialAmountS}
                    </div>
                    <div className='aResult'>
                        <p>Tag</p>
                        {roofMaterialAmountS}
                    </div>
                    <div className='aResult'>
                        <p>Gulve</p>
                        {floorMaterialAmountS}
                    </div>
                    <div className='aResult'>
                        <p>Fundament</p>
                        {foundationMaterialAmountS}
                    </div>
                    <div className='aResult'>
                        <p>Indervægge</p>
                        {inWallMaterialAmountS}
                    </div>
                </div>
                <div className='SumContainer'>
                    <div className='SumResult'>
                        <p>Samlet</p>
                        {mergedMaterialAmountS}
                    </div>
                    <div className='BarContainer'>
                        <Bar data={data} options={{ maintainAspectRatio: false }} width={400} height={400}/>
                    </div>
                </div>
                <Impacts materials={this.props.materials} building={this.props.building} amount={mergedMaterialsS}/>
            </div>
        )
    }

}

export default ResultsSimple