import React from 'react';
import '../../reset.css'
import './App.css';
import SearchBar from '../SearchBar/SearchBar';
import Building from '../Building/Building'
import Material from '../Material/Material'
import Simple from '../Simple/Simple'
import Advanced from '../Advanced/Advanced'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {imageSrc: '',
                  address: '',
                  area: '',
                  floors: '',
                  built: '',
                  selectAddress: false,
                  selectEdit: '',
                  location: [],
                  goBack: true,
                  floorHeight: 2.5,
                  roofType: 'Saddel',
                  foundationType: 'Linje',
                  energy: [],
                  notes: []};

    this.changeAddress = this.changeAddress.bind(this);
    this.changeArea = this.changeArea.bind(this);
    this.changeEdit = this.changeEdit.bind(this);
    this.changeBack = this.changeBack.bind(this);
    this.changeFloorHeight = this.changeFloorHeight.bind(this);
    this.changeRoofType = this.changeRoofType.bind(this);
    this.changeFoundationType = this.changeFoundationType.bind(this);
  }

  changeAddress(newAddress,newArea,newFloors,newBuilt,selectedAddress,newLocation,newGoBack,newFloorHeight,newRoofType, newFoundationType, newEnergy) {
    this.setState({address: newAddress})
    this.setState({area:newArea})
    this.setState({floors: newFloors})
    this.setState({built: newBuilt})
    this.setState({selectAddress: selectedAddress})
    this.setState({location: newLocation})
    this.setState({goBack: newGoBack})
    this.setState({floorHeight: newFloorHeight})
    this.setState({roofType: newRoofType})
    this.setState({foundationType: newFoundationType})
    this.setState({energy: newEnergy})
  } 

  changeArea(newArea) {
    this.setState({area:newArea})
  }

  changeFloorHeight(newFloorHeight) {
    this.setState({floorHeight: newFloorHeight})
  }

  changeRoofType(newRoofType) {
    this.setState({roofType: newRoofType})
  }

  changeFoundationType(newFoundationType) {
    this.setState({foundationType: newFoundationType})
  }

  changeEdit(newEdit) {
    this.setState({selectEdit: newEdit})
  }

  changeBack(newGoBack, selectedAddress) {
    this.setState({goBack: newGoBack})
    this.setState({selectAddress: selectedAddress})
  }

  render() {
    return (
      <div className="App">
          <SearchBar building={this.state} onChange={this.changeAddress}/>
          <div className='Info'>
            <Building building={this.state} onAreaChange={this.changeArea} onFloorHeightChange={this.changeFloorHeight}
            onRoofTypeChange={this.changeRoofType} onFoundationTypeChange={this.changeFoundationType}/>
            <Material building={this.state} onChange1={this.changeEdit} onChange2={this.changeBack}/>
            <Simple building={this.state} onChange={this.changeEdit}/>
            <Advanced building={this.state} onChange={this.changeEdit}/>
          </div>
      </div>
    );
  }
}

export default App;