import React from 'react'
import './FoundationTypeImage.css'
import linje from './Linje.jpg'
import punkt from './Punkt.jpg'
import stribe from './Stribe.jpg'

class FoundationTypeImage extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        if (this.props.foundationType === 'Linje') {
            return <img src={linje}/>
        } else if (this.props.foundationType === 'Punkt') {
            return <img src={punkt}/>
        } else if (this.props.foundationType === 'Stribe') {
            return <img src={stribe}/>
        } else {
            return <div></div>
        }
    }
}

export default FoundationTypeImage