import React from 'react'
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import './Simple.css'
import { simpleRoof, simpleWall, simpleFloor, simpleFoundation } from '../../functions/simpleFunctions'
import { Pie, Bar } from 'react-chartjs-2';

const wallOptions = [
    'Beton', 'Træ', 'Mursten'
]

const roofOptions = [
    'Skifer', 'Beton', 'Tagpap', 'Tegl'
];

const floorOptions = [
    'Beton', 'Træ'
]

const foundationOptions = [
    'Beton', 'Træ'
]

class Simple extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            wallMaterial: [],
            roofMaterial: [],
            floorMaterial: [],
            foundationMaterial: []
        }

        this.handleWallSelect = this.handleWallSelect.bind(this)
        this.handleRoofSelect = this.handleRoofSelect.bind(this)
        this.handleFloorSelect = this.handleFloorSelect.bind(this)
        this.handleFoundationSelect = this.handleFoundationSelect.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }

    handleWallSelect(newMaterial) {
        this.setState({wallMaterial: newMaterial.value})
    }

    handleRoofSelect(newMaterial) {
        this.setState({roofMaterial: newMaterial.value})
    }

    handleFloorSelect(newMaterial) {
        this.setState({floorMaterial: newMaterial.value})
    }

    handleFoundationSelect(newMaterial) {
        this.setState({foundationMaterial: newMaterial.value})
    }

    handleClick() {
        const edit = ''
        this.props.onChange(edit)
    }

    render() {

        //-----------------------------------------------------------------------------------
        const area = this.props.building.area
        const floors = this.props.building.floors
        const totalArea = area*floors
        const perimeter = Math.floor(Math.sqrt(area)*4)
        const wallArea = perimeter*(floors*2.5)
        const roofArea = Math.floor(area*1.1)
        const windowArea = Math.floor(wallArea*0.15)
        //-----------------------------------------------------------------------------------
        const wallMaterials = simpleWall(this.state.wallMaterial)
        const roofMaterials = simpleRoof(this.state.roofMaterial)
        const floorMaterials = simpleFloor(this.state.floorMaterial)
        const foundationMaterials = simpleFoundation(this.state.foundationMaterial)
        //-----------------------------------------------------------------------------------
        // Beton, Isolering, Træ, Mursten, Metal
        const wallMaterialAmount = wallMaterials.map(x => Math.floor(x * wallArea * 0.5));

        // Beton, Isolering, Træ, Metal
        const floorMaterialAmount = floorMaterials.map(x => Math.floor(x * totalArea*0.3));

        // Beton, Træ, Metal, Skifer, Tagpap, Tegl
        const roofMaterialAmount = roofMaterials.map(x => Math.floor(x * roofArea * 0.2));

        // Beton, Isolering, Træ, Metal
        const foundationMaterialAmount = foundationMaterials.map(x => Math.floor(x * area * 0.5));

        const wallData = {
            labels: ['Beton', 'Isolering', 'Træ', 'Mursten', 'Metal'],
            datasets: [
              {
                label: '# of Votes',
                data: wallMaterialAmount,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                  'rgba(153, 102, 255, 1)',
                ],
                borderWidth: 1,
              },
            ],
          }

        const roofData = {
            labels: ['Beton', 'Træ', 'Metal', 'Skifer', 'Tagpap', 'Tegl'],
            datasets: [
              {
                label: '# of Votes',
                data: roofMaterialAmount,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(100, 152, 255, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                  'rgba(153, 102, 255, 1)',
                  'rgba(100, 152, 255, 0.2)',
                ],
                borderWidth: 1,
              },
            ],
          }

        const floorData = {
            labels: ['Beton', 'Isolering', 'Træ', 'Metal'],
            datasets: [
              {
                label: '# of Votes',
                data: floorMaterialAmount,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                ],
                borderWidth: 1,
              },
            ],
          }

        const foundationData = {
            labels: ['Beton', 'Isolering', 'Træ', 'Metal'],
            datasets: [
              {
                label: '# of Votes',
                data: foundationMaterialAmount,
                backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                ],
                borderColor: [
                  'rgba(255, 99, 132, 1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                ],
                borderWidth: 1,
              },
            ],
          }

        const allData = {
          labels: ['1', '2', '3', '4', '5', '6'],
          datasets: [
            {
              label: 'Væg',
              data: wallMaterialAmount,
              backgroundColor: 'rgb(255, 99, 132)',
            },
            {
              label: 'Tag',
              data: roofMaterialAmount,
              backgroundColor: 'rgb(54, 162, 235)',
            },
            {
              label: 'Gulv',
              data: floorMaterialAmount,
              backgroundColor: 'rgb(75, 192, 192)',
            },
            {
              label: 'Fundament',
              data: foundationMaterialAmount,
              backgroundColor: 'rgb(75, 192, 192)',
            },
          ],
        }

        const allOptions = {
            scales: {
              yAxes: [
                {
                  stacked: true,
                  ticks: {
                    beginAtZero: true,
                  },
                },
              ],
              xAxes: [
                {
                  stacked: true,
                },
              ],
            },
        }
        


        if (this.props.building.selectAddress === true && this.props.building.selectEdit === 'simple') {
            return(
                <div className='simpleMain'>
                    <h2>REDIGER SIMPEL</h2>
                    <h3 className ='simpleInfo'>{this.props.building.address}</h3>
                    <div className='dropdownContainer'>
                        <div className='dropdownMenu'>
                            <p className='dropdownTitle'>Ydervægge</p>
                            <Dropdown options={wallOptions} onChange={this.handleWallSelect} placeholder="Vælg et materiale" />
                            <Pie data={wallData} className='pieChart' style='height: 140px'/>
                        </div>
                        <div className='dropdownMenu'>
                            <p className='dropdownTitle'>Tag</p>
                            <Dropdown options={roofOptions} onChange={this.handleRoofSelect} placeholder="Vælg et materiale" />
                            <Pie data={roofData} className='pieChart'/>
                        </div>
                        <div className='dropdownMenu'>
                            <p className='dropdownTitle'>Terrændæk</p>
                            <Dropdown options={floorOptions} onChange={this.handleFloorSelect} placeholder="Vælg et materiale" />
                            <Pie data={floorData} className='pieChart'/>
                        </div>
                        <div className='dropdownMenu'>
                            <p className='dropdownTitle'>Fundament</p>
                            <Dropdown options={foundationOptions} onChange={this.handleFoundationSelect} placeholder="Vælg et materiale" />
                            <Pie data={foundationData} className='pieChart'/>
                        </div>
                    </div>
                    <div className='barContainer'>
                      <Bar data={allData} options={allOptions} width={400} height={400}/>
                    </div>
                    <div className='simpleButtonContainer'>
                        <a className="simpleButton" onClick={this.handleClick}>Tilbage</a>
                    </div>
                </div>
            )
        } else {
            return(
                <div></div>
            )
        }
    }
}

export default Simple