import React from 'react'
import './Impacts.css'

class Impacts extends React.Component {
    constructor(props) {
        super(props)
        
        this.state = {emissions: {},
                     complete: false}

        this.multiplyObj = this.multiplyObj.bind(this)
        this.getMaterialEmission = this.getMaterialEmission.bind(this)
        this.runEmissions = this.runEmissions.bind(this)
        this.runPrices = this.runPrices.bind(this)
        this.removeMissing = this.removeMissing.bind(this)
        this.sumObjects = this.sumObjects.bind(this)
    }

    multiplyObj(obj,val) {
        for (let key in obj) {
            if (typeof obj[key] == 'number') {
              obj[key] *= val;
            }
        } return obj
    }

    getMaterialEmission(arr,obj) {
        const newEmission = {}
        const newEmissions = obj
        for (var x of arr) {
            if (x[0] === 'Beton') {
                newEmission.Beton = this.multiplyObj(newEmissions.Beton, x[1])
            } 
            if (x[0] === 'Træ') {
                newEmission.Træ = this.multiplyObj(newEmissions.Træ, x[1])
            } 
            if (x[0] === 'Skifer') {
                newEmission.Skifer = this.multiplyObj(newEmissions.Skifer, x[1])
            } 
            if (x[0] === 'Tegl') {
                newEmission.Tegl = this.multiplyObj(newEmissions.Tegl, x[1])
            } 
            if (x[0] === 'Sten') {
                newEmission.Fliser = this.multiplyObj(newEmissions.Fliser, x[1])
            } 
            if (x[0] === 'Isolering') {
                newEmission.Isolering = this.multiplyObj(newEmissions.Isolering, x[1])
            } 
            if (x[0] === 'Pap') {
                newEmission.Tagpap = this.multiplyObj(newEmissions.Tagpap, x[1])
            } 
            if (x[0] === 'Mursten') {
                newEmission.Mursten = this.multiplyObj(newEmissions.Mursten, x[1])
            } 
            if (x[0] === 'Metal') {
                newEmission.Metal = this.multiplyObj(newEmissions.Metal, x[1])
            } 
            if (x[0] === 'Gips') {
                newEmission.Gips = this.multiplyObj(newEmissions.Gips, x[1])
            }
        }
        return newEmission
    } 

    runEmissions() {
        const emissions = {
    
            //M2 2cm
            Fliser:     {GWP: 6.18*50,
                        ODP: (4.96*Math.exp(-14))*50,
                        POCP: 0.000326*50,
                        AP: 0.00613*50,
                        EP: 0.00119*50,
                        ADPE: 7.81*Math.exp(-7)*50,
                        ADPF: 97.66*50,
                        PERT: 9.186*50,
                        PENRT: 100.42*50,
                        RSF: 0*50,
                        NRSF: 0*50},
        
            Beton:      {GWP: 197,
                        ODP: 5.36*Math.exp(-8),
                        POCP: 0.023,
                        AP: 0.287,
                        EP: 0.0535,
                        ADPE: 0.000634,
                        ADPF: 900,
                        PERT: 190,
                        PENRT: 999,
                        RSF: 193,
                        NRSF: 348},
        
            Træ:        {GWP: 0,
                        ODP: 6.49*Math.exp(-12),
                        POCP: 0.059,
                        AP: 0.667,
                        EP: 0.152,
                        ADPE: 6.75*Math.exp(-5),
                        ADPF: 2494,
                        PERT: 12719,
                        PENRT: 2853,
                        RSF: 0,
                        NRSF: 0},
            //M2 2cm
            Tegl:       {GWP: 15.87*50,
                        ODP: (1.14*Math.exp(-13))*50,
                        POCP: 0.003*50,
                        AP: 0.0123*50,
                        EP: 0.0023*50,
                        ADPE: 1.84*Math.exp(-6)*50,
                        ADPF: 242*50,
                        PERT: 21.34*50,
                        PENRT: 248.8*50,
                        RSF: 0*50,
                        NRSF: 0*50},
            //M2 2cm
            Skifer:     {GWP: 15.03*50,
                        ODP: (2.21*Math.exp(-13))*50,
                        POCP: 0.0088*50,
                        AP: 0.088*50,
                        EP: 0.02*50,
                        ADPE: 4.55*Math.exp(-6)*50,
                        ADPF: 189*50,
                        PERT: 49*50,
                        PENRT: 202.9*50,
                        RSF: 0*50,
                        NRSF: 0}*50,
        
            Isolering:  {GWP: 40,
                        ODP: 6.18*Math.exp(-13),
                        POCP: 0.0097,
                        AP: 0.18,
                        EP: 0.026,
                        ADPE: 8.53*Math.exp(-6),
                        ADPF: 459,
                        PERT: 78,
                        PENRT: 481.1,
                        RSF: 0.0068,
                        NRSF: 0},
            //M2 1cm
            Tagpap:     {GWP: 5.36*100,
                        ODP: (4.9*Math.exp(-14))*100,
                        POCP: 0.0015*100,
                        AP: 0.0084*100,
                        EP: 0.0011*100,
                        ADPE: 1.1*Math.exp(-6)*100,
                        ADPF: 254*100,
                        PERT: 9.46*100,
                        PENRT: 258*100,
                        RSF: 0*100,
                        NRSF: 0*100},
        
            Mursten:    {GWP: 528,
                        ODP: 3.03*Math.exp(-12),
                        POCP: 0.079,
                        AP: 0.45,
                        EP: 0.084,
                        ADPE: 5.4*Math.exp(-5),
                        ADPF: 7980,
                        PERT: 574,
                        PENRT: 8150,
                        RSF: 0,
                        NRSF: 0},
            
            Metal:      {GWP: 1125,
                        ODP: 1.95*Math.exp(-11),
                        POCP: 0.4,
                        AP: 2.164,
                        EP: 0.219,
                        ADPE: 0.000049,
                        ADPF: 10200,
                        PERT: 1516,
                        PENRT: 11470,
                        RSF: 0,
                        NRSF: 0},
            //2cm
            Gips:       {GWP: 1.54*50,
                        ODP: (1.44*Math.exp(-14))*50,
                        POCP: 0.00001*50,
                        AP: 0.0028*50,
                        EP: 0.000088*50,
                        ADPE: 2.48*Math.exp(-7)*50,
                        ADPF: 32.68*50,
                        PERT: 9.8*50,
                        PENRT: 33.5*50,
                        RSF: 0,
                        NRSF: 0},
        }

        const newEmission = this.getMaterialEmission(this.props.amount,emissions)

        this.setState({emissions: newEmission})
        this.setState({complete: true})
    }

    runPrices(array) {
        const newArray = []
        const Beton = ['Beton',0]
        const Træ = ['Træ',0]
        const Skifer = ['Skifer',0]
        const Tegl = ['Tegl',0]
        const Isolering = ['Isolering',0]
        const Tagpap = ['Pap',0]
        const Mursten = ['Mursten',0]
        const Gips = ['Gips', 0]
        const Metal = ['Metal', 0]
        const Sten = ['Sten', 0]

        for (var x of array) {
            if (x[0] === 'Træ') {
                Træ[1] = x[1]*70
            }
            if (x[0] === 'Beton') {
                Beton[1] = x[1]*60
            }
            if (x[0] === 'Skifer') {
                Skifer[1] = x[1]*80
            }
            if (x[0] === 'Tegl') {
                Tegl[1] = x[1]*85
            }
            if (x[0] === 'Isolering') {
                Isolering[1] = x[1]*55
            }
            if (x[0] === 'Tagpap') {
                Tagpap[1] = x[1]*20
            }
            if (x[0] === 'Mursten') {
                Mursten[1] = x[1]*70
            }
            if (x[0] === 'Gips') {
                Gips[1] = x[1]*40
            }
            if (x[0] === 'Metal') {
                Metal[1] = x[1]*200
            }
            if (x[0] === 'Sten') {
                Sten[1] = x[1]*85
            }             
        }
        newArray.push(Beton) 
        newArray.push(Træ)
        newArray.push(Skifer)
        newArray.push(Tegl)
        newArray.push(Isolering)
        newArray.push(Tagpap)
        newArray.push(Mursten)
        newArray.push(Metal)
        newArray.push(Gips)
        newArray.push(Sten)
        return newArray
    }

    removeMissing(array) {
        const newArray = []
        for (var x of array) {
            if (x[1] !== 0) {
                newArray.push(x)
            }
        } return newArray
    }

    sumObjects(...objs) {
        return objs.reduce((a, b) => {
          for (let k in b) {
            if (b.hasOwnProperty(k))
              a[k] = (a[k] || 0) + b[k];
          }
          return a;
        }, {});
    }

    render() { 
        if (this.state.complete === false) {
        return(
            <div className='ImpactSection'>
                <div className='ImpactHeader'>
                    <h1>Besparelser</h1>
                </div>
                <a className='emissionUpdate' onClick={this.runEmissions}>Beregn/Opdater</a>
                <p></p>
            </div>
        )
        } else if (this.state.complete === true) {
            return(
                <div className='ImpactSection'>
                    <div className='ImpactHeader'>
                        <h1>Besparelser</h1>
                    </div>
                    <div>
                        <a className='emissionUpdate' onClick={this.runEmissions}>Beregn/Opdater</a>
                    </div>
                    <div className='emissionContainer'>
                        <div className='climateEmission'>
                            <p className='emissionTitle'>Klima</p>
                            <p>{Object.entries(this.sumObjects(this.state.emissions.Beton,this.state.emissions.Isolering,this.state.emissions.Metal,this.state.emissions.Fliser,
                                this.state.emissions.Træ,this.state.emissions.Tegl,this.state.emissions.Skifer,this.state.emissions.Tagpap,this.state.emissions.Mursten,
                                this.state.emissions.Gips)).map((item) => <li>{item[0]}: {(item[1]).toFixed(1)}</li>)}</p>
                        </div>
                        <div className='financialEmission'>
                            <p className='emissionTitle'>Financielt</p>
                            <p>{this.removeMissing(this.runPrices(this.props.amount)).map((item) => <li>{item[0]}: {(item[1].toFixed(1))} DKK</li>)}</p>
                        </div>
                    </div>
                </div>
            )
        }
    }
}

export default Impacts