import React from 'react'
import './Material.css'

class Material extends React.Component {
  constructor(props) {
    super(props);

    this.handleSimpleClick = this.handleSimpleClick.bind(this);
    this.handleAdvancedClick = this.handleAdvancedClick.bind(this);
    this.handleBackClick = this.handleBackClick.bind(this);
  }

  handleSimpleClick() {
    const edit = 'simple'
    this.props.onChange1(edit)
  }

  handleAdvancedClick() {
    const edit = 'advanced'
    this.props.onChange1(edit)
  }

  handleBackClick() {
    const goBack = true
    const selectAddress = false
    this.props.onChange2(goBack,selectAddress)
  }


  render() {
    if (this.props.building.selectAddress === true && this.props.building.selectEdit === '') {
    return(
      <div className="mainMaterial">
        <div className="buttonDiv">
          <a className="buttonMaterial" onClick={this.handleBackClick}>Tilbage</a>
        </div>
      </div>
    )
    } else {
      return(
        <div></div>
      )
    }
  }
}
export default Material