import React from 'react'
import Dropdown from 'react-dropdown';
import './MaterialList.css'
import Toggle from 'react-toggle'
import Results from '../Results/Results'
import ResultsSimple from '../ResultsSimple/ResultsSimple'
import UpdateButton from './updateButton'
import {RangeStepInput} from 'react-range-step-input';
import { db } from "../../util/firebase";
import { simpleRoof, simpleWall, simpleFloor, simpleFoundation, simpleInWall } from '../../functions/simpleFunctions'
import { IconContext } from "react-icons";
import { BsFillInfoCircleFill } from "react-icons/bs";
import ReactTooltip from 'react-tooltip';

const Options = ['Beton', 'Træ', 'Skifer', 'Tegl', 'Isolering', 'Pap', 'Mursten', 'Metal', 'Gips', 'Sten']
const wallOptions = ['Beton', 'Træ', 'Mursten']
const floorOptions = ['Beton', 'Træ', 'Sten']
const roofOptions = ['Tegl', 'Skifer', 'Tagpap']
const foundationOptions = ['Beton', 'Skrue']
const inWallOptions = ['Beton', 'Gips', 'Træ', 'Mursten']
const windowOptions = ['1-lag', '2-lag', '3-lag']

class MaterialList extends React.Component {
    constructor(props) {
        super(props)

        this.state = {wall: [],
                      wallS: [],
                      wallM: '',
                      wallP: 0.1,


                      roof: [],
                      roofS: [],
                      roofM: '',
                      roofP: 0.1,

                      floor: [],
                      floorS: [],
                      floorM: '',
                      floorP: 0.1,

                      foundation: [],
                      foundationS: [],
                      foundationM: '',
                      foundationP: 0.1,

                      inwall: [],
                      inwallS: [],
                      inwallM: '',
                      inwallP: 0.1,

                      windows: [],
                    
                      advanced: false}
                      
        this.addWallItem = this.addWallItem.bind(this)
        this.addWallMaterial = this.addWallMaterial.bind(this)
        this.addWallPercentage = this.addWallPercentage.bind(this)
        this.removeWallItem = this.removeWallItem.bind(this)
        this.addSimpleWall = this.addSimpleWall.bind(this)
        
        this.addRoofItem = this.addRoofItem.bind(this)
        this.addRoofMaterial = this.addRoofMaterial.bind(this)
        this.addRoofPercentage = this.addRoofPercentage.bind(this)
        this.removeRoofItem = this.removeRoofItem.bind(this)
        this.addSimpleRoof = this.addSimpleRoof.bind(this)

        this.addFloorItem = this.addFloorItem.bind(this)
        this.addFloorMaterial = this.addFloorMaterial.bind(this)
        this.addFloorPercentage = this.addFloorPercentage.bind(this)
        this.removeFloorItem = this.removeFloorItem.bind(this)
        this.addSimpleFloor = this.addSimpleFloor.bind(this)
        
        this.addFoundationItem = this.addFoundationItem.bind(this)
        this.addFoundationMaterial = this.addFoundationMaterial.bind(this)
        this.addFoundationPercentage = this.addFoundationPercentage.bind(this)
        this.removeFoundationItem = this.removeFoundationItem.bind(this)
        this.addSimpleFoundation = this.addSimpleFoundation.bind(this)

        this.addInWallItem = this.addInWallItem.bind(this)
        this.addInWallMaterial = this.addInWallMaterial.bind(this)
        this.addInWallPercentage = this.addInWallPercentage.bind(this)
        this.removeInWallItem = this.removeInWallItem.bind(this)
        this.addSimpleInWall = this.addSimpleInWall.bind(this)

        this.addWindowItem = this.addWindowItem.bind(this)
        

        this.toggleAdvanced = this.toggleAdvanced.bind(this)
        this.toggleSimple = this.toggleSimple.bind(this)

        this.isAbove100 = this.isAbove100.bind(this)

        this.updateData = this.updateData.bind(this)
    }

      componentDidMount() {
        db.ref(this.props.building.address).on("value", snapshot => {
            let allInfo = [];
            snapshot.forEach(snap => {
              allInfo.push(snap.val());
            });
            this.setState({ wall: allInfo[6].wall,
                            roof: allInfo[6].roof,
                            floor: allInfo[6].floor,
                            foundation: allInfo[6].foundation,
                            inwall: allInfo[6].inwall,
                            windows: allInfo[6].windows,

                            wallS: allInfo[6].wallS,
                            roofS: allInfo[6].roofS,
                            floorS: allInfo[6].floorS,
                            foundationS: allInfo[6].foundationS,
                            inwallS: allInfo[6].inwallS});
          });
      }

      updateData() {
        const wall = this.state.wall
        const roof = this.state.roof
        const floor = this.state.floor
        const foundation = this.state.foundation

            return db
            .ref(`${this.props.building.address}/materials`)
            .update(
                {wall,roof,floor,foundation},
            )
            .then(_ => {
                this.setState({ wall: this.state.wall,
                                roof: this.state.roof,
                                floor: this.state.floor,
                                foundation: this.state.foundation});
            });
      }
      

    //-------------------WALL-----------------------
    //#region
    addWallItem() {
        this.setState({ wall: [...this.state.wall, [this.state.wallM, this.state.wallP]] }) 
    }

    addWallMaterial(item) {
        this.setState({ wallM: item.value }) 
    }

    addWallPercentage(item) {
        const newVal = item.target.value;
        this.setState({ wallP: newVal }) 
    }

    removeWallItem() {
        this.setState({ wall: this.state.wall.slice(0,this.state.wall.length-1)})
    }

    addSimpleWall(item) {
        this.setState({ wallS: item.value})
    }
    //#endregion

    //-------------------ROOF-----------------------
    //#region
    addRoofItem(item) {
        this.setState({ roof: [...this.state.roof, [this.state.roofM, this.state.roofP]] }) 
    }

    addRoofMaterial(item) {
        this.setState({ roofM: item.value }) 
    }

    addRoofPercentage(item) {
        this.setState({ roofP: item.target.value }) 
    }

    removeRoofItem() {
        this.setState({ roof: this.state.roof.slice(0,this.state.roof.length-1)})
    }

    addSimpleRoof(item) {
        this.setState({ roofS: item.value})
    }
    //#endregion

    //-------------------FLOOR-----------------------
    //#region
    addFloorItem(item) {
        this.setState({ floor: [...this.state.floor, [this.state.floorM, this.state.floorP]] }) 
    }

    addFloorMaterial(item) {
        this.setState({ floorM: item.value})
    }

    addFloorPercentage(item) {
        this.setState({ floorP: item.target.value})
    }

    removeFloorItem() {
        this.setState({ floor: this.state.floor.slice(0,this.state.floor.length-1)})
    }

    addSimpleFloor(item) {
        this.setState({ floorS: item.value})
    }
    //#endregion
    
    //-------------------FOUNDATION-----------------------
    //#region
    addFoundationItem(item) {
        this.setState({ foundation: [...this.state.foundation, [this.state.foundationM, this.state.foundationP]] }) 
    }

    addFoundationMaterial(item) {
        this.setState({ foundationM: item.value})
    }

    addFoundationPercentage(item) {
        this.setState({ foundationP: item.target.value})
    }

    removeFoundationItem() {
        this.setState({ foundation: this.state.foundation.slice(0,this.state.foundation.length-1)})
    }

    addSimpleFoundation(item) {
        this.setState({ foundationS: item.value})
    }
    //#endregion

    //-------------------INWALL-----------------------
    //#region
    addInWallItem() {
        this.setState({ inwall: [...this.state.inwall, [this.state.inwallM, this.state.inwallP]] }) 
    }

    addInWallMaterial(item) {
        this.setState({ inwallM: item.value }) 
    }

    addInWallPercentage(item) {
        const newVal = item.target.value;
        this.setState({ inwallP: newVal }) 
    }

    removeInWallItem() {
        this.setState({ inwall: this.state.inwall.slice(0,this.state.inwall.length-1)})
    }

    addSimpleInWall(item) {
        this.setState({ inwallS: item.value})
    }
    //#endregion

    //-------------------WINDOWS-----------------------
    //#region
    addWindowItem(item) {
        this.setState({ windows: item.value})
    }
    //#endregion

    //-------------------TOGGLE-----------------------
    //#region
    toggleAdvanced() {
        this.setState({advanced: true})
    }

    toggleSimple() {
        this.setState({advanced: false})
    }
    //#endregion

    //-------------------FUNCTIONS-----------------------
    isAbove100(number) {
        if (number >= 0) {
            return 'Mangler: '+number+'%'
        } else {
            return 'OVER 100%, FJERN MATERIALER'
        }
    }

    render () {
        if (this.state.advanced == true) {
        return (
            <div>
                <div className='MaterialHeader'>
                    <h1>Materialer</h1>
                    <IconContext.Provider value={{size: "1.6rem", color: "white", className: "materialInfo" }}>
                            <div>
                                <BsFillInfoCircleFill data-tip="" data-for='2'/>
                                <ReactTooltip place="right" type="info" effect="solid" border='true' id='2'>
                                    <ol className='ToolTipList' type='A'>
                                    <li>Materialer vælges på 2 måder - simpelt eller avanceret</li>
                                    <li>I den simple udgave vælges et preset for hvert materiale, som herefter <br></br> bestemmer hvilke materialer og tykkelser der indgår</li>
                                    <li>I den avancerede udgave tilføjer brugeren selv materialer og materialetykkelser til hver af bygningsdelene</li>
                                    </ol>
                                </ReactTooltip>
                            </div>
                    </IconContext.Provider>
                </div>
                <div className='ToggleButton'>
                        <p>Avanceret</p>
                        <Toggle
                            id='material-status'
                            onChange={this.toggleSimple} />
                </div>
                <div className='materialLists'>
                    <div className='materialSection'>
                        <h2>Ydervægge</h2>
                        <div className='dropCreate'>
                            <Dropdown className='drop1' options={Options} onChange={this.addWallMaterial} placeholder={'Vælg et materiale'}/>
                            <div className='sliderSection'>
                                <RangeStepInput 
                                    min={0.01} max={1}
                                    value={this.state.wallP} step={0.01}
                                    onChange={this.addWallPercentage}
                                    style={{width: 220 + 'px'}}
                                />
                                <a className='input-group-text'>{this.state.wallP}m</a>
                            </div>
                        </div>
                        <div>{this.state.wall.map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        <button onClick={this.addWallItem}>Tilføj</button>
                        <button onClick={this.removeWallItem}>Fjern</button>
                    </div>
                    <div className='materialSection'>
                        <h2>Tag</h2>
                        <div className='dropCreate'>
                            <Dropdown className='drop1' options={Options} onChange={this.addRoofMaterial} placeholder={'Vælg et materiale'}/>
                            <div className='sliderSection'>
                                <RangeStepInput 
                                    min={0.01} max={1}
                                    value={this.state.roofP} step={0.01}
                                    onChange={this.addRoofPercentage}
                                    style={{width: 220 + 'px'}}
                                />
                                <a className='input-group-text'>{this.state.roofP}m</a>
                            </div>
                        </div>
                        <div>{this.state.roof.map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        <button onClick={this.addRoofItem}>Tilføj</button>
                        <button onClick={this.removeRoofItem}>Fjern</button>
                    </div>
                    <div className='materialSection'>
                        <h2>Gulv</h2>
                        <div className='dropCreate'>
                            <Dropdown className='drop1' options={Options} onChange={this.addFloorMaterial} placeholder={'Vælg et materiale'}/>
                            <div className='sliderSection'>
                                <RangeStepInput 
                                    min={0.01} max={1}
                                    value={this.state.floorP} step={0.01}
                                    onChange={this.addFloorPercentage}
                                    style={{width: 220 + 'px'}}
                                />
                                <a className='input-group-text'>{this.state.floorP}m</a>
                            </div>
                        </div>
                        <div>{this.state.floor.map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        <button onClick={this.addFloorItem}>Tilføj</button>
                        <button onClick={this.removeFloorItem}>Fjern</button>
                    </div>
                    <div className='materialSection'>
                        <h2>Fundament</h2>
                        <div className='dropCreate'>
                            <Dropdown className='drop1' options={Options} onChange={this.addFoundationMaterial} placeholder={'Vælg et materiale'}/>
                            <div className='sliderSection'>
                                <RangeStepInput 
                                    min={0.01} max={1}
                                    value={this.state.foundationP} step={0.01}
                                    onChange={this.addFoundationPercentage}
                                    style={{width: 220 + 'px'}}
                                />
                                <a className='input-group-text'>{this.state.foundationP}m</a>
                            </div>
                        </div>
                        <div>{this.state.foundation.map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        <button onClick={this.addFoundationItem}>Tilføj</button>
                        <button onClick={this.removeFoundationItem}>Fjern</button>
                    </div>
                    <div className='materialSection'>
                        <h2>Indervægge</h2>
                        <div className='dropCreate'>
                            <Dropdown className='drop1' options={Options} onChange={this.addInWallMaterial} placeholder={'Vælg et materiale'}/>
                            <div className='sliderSection'>
                                <RangeStepInput 
                                    min={0.01} max={1}
                                    value={this.state.inwallP} step={0.01}
                                    onChange={this.addInWallPercentage}
                                    style={{width: 220 + 'px'}}
                                />
                                <a className='input-group-text'>{this.state.inwallP}m</a>
                            </div>
                        </div>
                        <div>{this.state.inwall.map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        <button onClick={this.addInWallItem}>Tilføj</button>
                        <button onClick={this.removeInWallItem}>Fjern</button>
                    </div>
                    <div className='materialSection'>
                            <h2>Vinduer</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.windows}</p>
                                <Dropdown className='drop3' options={windowOptions} onChange={this.addWindowItem} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{this.state.windows}</div>
                    </div>
                </div>
                <Results materials={this.state} building={this.props.building}/>
                <UpdateButton materials={this.state} building={this.props.building}/>
            </div>
        )
        } else {
            return(
                <div>
                    <div className='MaterialHeader'>
                        <h1>Materialer</h1>
                        <IconContext.Provider value={{size: "1.6rem", color: "white", className: "materialInfo" }}>
                                <div>
                                <BsFillInfoCircleFill data-tip="" data-for='3'/>
                                <ReactTooltip place="right" type="info" effect="solid" border='true' id='3'>
                                    <ul className='ToolTipList' type='A'>
                                        <li>Materialer vælges på 2 måder - simpelt eller avanceret</li>
                                        <li>I den simple udgave vælges et preset for hvert materiale, som herefter <br></br> bestemmer hvilke materialer og tykkelser der indgår</li>
                                        <li>I den avancerede udgave tilføjer brugeren selv materialer og materialetykkelser til hver af bygningsdelene</li>
                                    </ul>
                                </ReactTooltip>
                                </div>
                        </IconContext.Provider>
                    </div>
                    <div className='ToggleButton'>
                        <p>Avanceret</p>
                        <Toggle
                            id='material-status'
                            onChange={this.toggleAdvanced} />
                    </div>
                    <div className='materialLists'>
                        <div className='materialSection'>
                            <h2>Ydervægge</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.wallS}</p>
                                <Dropdown className='drop2' options={wallOptions} onChange={this.addSimpleWall} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{simpleWall(this.state.wallS).map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        </div>
                        <div className='materialSection'>
                            <h2>Tag</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.roofS}</p>
                                <Dropdown className='drop2' options={roofOptions} onChange={this.addSimpleRoof} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{simpleRoof(this.state.roofS).map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        </div>
                        <div className='materialSection'>
                            <h2>Gulv</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.floorS}</p>
                                <Dropdown className='drop2' options={floorOptions} onChange={this.addSimpleFloor} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{simpleFloor(this.state.floorS).map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        </div>
                        <div className='materialSection'>
                            <h2>Fundament</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.foundationS}</p>
                                <Dropdown className='drop2' options={foundationOptions} onChange={this.addSimpleFoundation} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{simpleFoundation(this.state.foundationS).map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        </div>
                        <div className='materialSection'>
                            <h2>Indervægge</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.inwallS}</p>
                                <Dropdown className='drop2' options={inWallOptions} onChange={this.addSimpleInWall} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{simpleInWall(this.state.inwallS).map((item) => <li>{item[0]} {item[1]}m</li>)}</div>
                        </div>
                        <div className='materialSection'>
                            <h2>Vinduer</h2>
                            <div className='dropCreate'>
                                <p>Nuværende preset: {this.state.windows}</p>
                                <Dropdown className='drop2' options={windowOptions} onChange={this.addWindowItem} placeholder={'Vælg et preset'}/>
                            </div>
                            <div>{this.state.windows}</div>
                        </div>
                    </div>
                    <ResultsSimple materials={this.state} building={this.props.building}/>
                    <UpdateButton materials={this.state} building={this.props.building}/>
                </div>
                
            )
        }
    }
}

export default MaterialList