import React from 'react'

class Download extends React.Component {
    constructor(props) {
      super(props)
      
      const defaultFileType = "json"; 
      this.fileNames = {
          json: "bygningspas.json",
      }    

      this.state = {
        fileType: defaultFileType,
        fileDownloadUrl: null,
        status: "",
          data: [{Væg: this.props.materials.wall},{Tag: this.props.materials.roof},{Gulv: this.props.materials.floor},{Fundament: this.props.materials.foundation}],
          data2: [this.props.amount]
      }
        this.download = this.download.bind(this);
    }
    
    download (event) {
      event.preventDefault();
        // Prepare the file
      let output;
      if (this.state.fileType === "json") {
          output = JSON.stringify({Bygningsdele: this.state.data, Materialer: this.state.data2}, 
            null, 4);
      }
      // Download it
      const blob = new Blob([output]);
      const fileDownloadUrl = URL.createObjectURL(blob);
      this.setState ({fileDownloadUrl: fileDownloadUrl}, 
        () => {
          this.dofileDownload.click(); 
          URL.revokeObjectURL(fileDownloadUrl);  // free up storage--no longer needed.
          this.setState({fileDownloadUrl: ""})
      })    
    }
    
    render() {
      return (
        <div className='downloadSection'>
          <form>
            <button className='buttonDownload' onClick={this.download} >
              Download
            </button>
            
            <a className="hidden"
               download={this.fileNames[this.state.fileType]}
               href={this.state.fileDownloadUrl}
               ref={e=>this.dofileDownload = e}
            ></a>
          </form>
          <pre className="status">{this.state.status}</pre>
        </div>
        )
    }
  }

  export default Download